.spinner-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-transform: translateY(-45px);
      -ms-transform: translateY(-45px);
          transform: translateY(-45px);
}

.content-loader {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 90px;
  height: 90px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transform: rotateX(45deg) rotate(45deg);
          transform: rotateX(45deg) rotate(45deg);
}

.spinner-box {
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
  height: 30px;
  background: #FED750;
  -webkit-animation: spinner-move 2s cubic-bezier(0.785, 0.135, 0.15, 0.86) infinite both;
          animation: spinner-move 2s cubic-bezier(0.785, 0.135, 0.15, 0.86) infinite both;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.spinner-box:nth-child(1) {
  -webkit-animation-delay: -1s;
          animation-delay: -1s;
}

.spinner-box:nth-child(2) {
  -webkit-animation-delay: -2s;
          animation-delay: -2s;
}

.spinner-box:nth-child(3) {
  -webkit-animation-delay: -3s;
          animation-delay: -3s;
}

.spinner-box:before, .spinner-box:after {
  display: block;
  content: "";
  position: absolute;
  width: 30px;
  height: 30px;
}

.spinner-box:before {
  top: 100%;
  left: 0;
  background: #E6A32F;
  -webkit-transform-origin: center top;
      -ms-transform-origin: center top;
          transform-origin: center top;
  -webkit-transform: rotateX(-90deg);
          transform: rotateX(-90deg);
}

.spinner-box:after {
  top: 0;
  left: 100%;
  background: #C87932;
  -webkit-transform-origin: center left;
      -ms-transform-origin: center left;
          transform-origin: center left;
  -webkit-transform: rotateY(90deg);
          transform: rotateY(90deg);
}

@-webkit-keyframes spinner-move {
  0%, 100% {
    -webkit-transform: none;
            transform: none;
  }
  12.5% {
    -webkit-transform: translate(30px, 0);
            transform: translate(30px, 0);
  }
  25% {
    -webkit-transform: translate(60px, 0);
            transform: translate(60px, 0);
  }
  37.5% {
    -webkit-transform: translate(60px, 30px);
            transform: translate(60px, 30px);
  }
  50% {
    -webkit-transform: translate(60px, 60px);
            transform: translate(60px, 60px);
  }
  62.5% {
    -webkit-transform: translate(30px, 60px);
            transform: translate(30px, 60px);
  }
  75% {
    -webkit-transform: translate(0, 60px);
            transform: translate(0, 60px);
  }
  87.5% {
    -webkit-transform: translate(0, 30px);
            transform: translate(0, 30px);
  }
}

@keyframes spinner-move {
  0%, 100% {
    -webkit-transform: none;
            transform: none;
  }
  12.5% {
    -webkit-transform: translate(30px, 0);
            transform: translate(30px, 0);
  }
  25% {
    -webkit-transform: translate(60px, 0);
            transform: translate(60px, 0);
  }
  37.5% {
    -webkit-transform: translate(60px, 30px);
            transform: translate(60px, 30px);
  }
  50% {
    -webkit-transform: translate(60px, 60px);
            transform: translate(60px, 60px);
  }
  62.5% {
    -webkit-transform: translate(30px, 60px);
            transform: translate(30px, 60px);
  }
  75% {
    -webkit-transform: translate(0, 60px);
            transform: translate(0, 60px);
  }
  87.5% {
    -webkit-transform: translate(0, 30px);
            transform: translate(0, 30px);
  }
}

.centered {
	width:400px;
	height:300px;
	position:absolute;
	top:50%;
	left:50%;
	-webkit-transform:translate(-50%,-50%);
	    -ms-transform:translate(-50%,-50%);
	        transform:translate(-50%,-50%);
}
.blob-1,.blob-2 {
	width:70px;
	height:70px;
	position:absolute;
	background:#3d3f57;
	border-radius:50%;
	top:50%;left:50%;
	-webkit-transform:translate(-50%,-50%);
	    -ms-transform:translate(-50%,-50%);
	        transform:translate(-50%,-50%);
}

.blob-1 {
	left:20%;
	-webkit-animation:osc-l 2.2s cubic-bezier(0.68, -0.55, 0.265, 1.55) infinite;
	        animation:osc-l 2.2s cubic-bezier(0.68, -0.55, 0.265, 1.55) infinite;
}
.blob-2 {
	left:80%;
	-webkit-animation:osc-r 2.2s cubic-bezier(0.68, -0.55, 0.265, 1.55) infinite;
	        animation:osc-r 2.2s cubic-bezier(0.68, -0.55, 0.265, 1.55) infinite;
	background:#737594;
}

@-webkit-keyframes osc-l {
	0%{left:20%;}
	50%{left:50%;}
	100%{left:20%;}
}

@keyframes osc-l {
	0%{left:20%;}
	50%{left:50%;}
	100%{left:20%;}
}
@-webkit-keyframes osc-r {
	0%{left:80%;}
	50%{left:50%;}
	100%{left:80%;}
}
@keyframes osc-r {
	0%{left:80%;}
	50%{left:50%;}
	100%{left:80%;}
}


